@charset "UTF-8";
@keyframes fade__bottom {
  0% {
    opacity: 0;
    transform: translateY(50px);
    -webkit-transform: translateY(50px);
    -moz-transform: translateY(50px);
    -ms-transform: translateY(50px);
    -o-transform: translateY(50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
    -webkit-transform: translateY(0);
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -o-transform: translateY(0);
  }
}
@keyframes fade__left {
  0% {
    opacity: 0;
    transform: translateX(-50px);
    -webkit-transform: translateX(-50px);
    -moz-transform: translateX(-50px);
    -ms-transform: translateX(-50px);
    -o-transform: translateX(-50px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
    -ms-transform: translateX(0);
    -o-transform: translateX(0);
  }
}
@keyframes fade__top {
  0% {
    opacity: 0;
    transform: translateY(-50px);
    -webkit-transform: translateY(-50px);
    -moz-transform: translateY(-50px);
    -ms-transform: translateY(-50px);
    -o-transform: translateY(-50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
    -webkit-transform: translateY(0);
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -o-transform: translateY(0);
  }
}
@keyframes fade__right {
  0% {
    opacity: 0;
    transform: translateX(50px);
    -webkit-transform: translateX(50px);
    -moz-transform: translateX(50px);
    -ms-transform: translateX(50px);
    -o-transform: translateX(50px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
    -ms-transform: translateX(0);
    -o-transform: translateX(0);
  }
}
@keyframes slide__center {
  0% {
    opacity: 0;
    transform: translateZ(-1400px);
    -webkit-transform: translateZ(-1400px);
    -moz-transform: translateZ(-1400px);
    -ms-transform: translateZ(-1400px);
    -o-transform: translateZ(-1400px);
  }
  100% {
    opacity: 1;
    transform: translateZ(0);
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
  }
}
@keyframes fade__in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@media screen and (min-width: 32rem) {
  h2 .featured {
    padding-left: 0.625rem;
    padding-right: 0.625rem;
    border-left: 0.3125rem solid #9346dc;
  }
}
@media screen and (min-width: 61rem) {
  h2 .featured {
    display: block;
  }
}

#home h3:not(.card-title) {
  color: #9346dc;
  position: relative;
  padding-left: 0.9375rem;
}
#home h3:not(.card-title)::before {
  content: "";
  width: 0;
  height: 0;
  display: inline-block;
  border-top: 0.4375rem solid transparent;
  border-bottom: 0.4375rem solid transparent;
  border-left: 0.6875rem solid #9346dc;
  position: absolute;
  margin-top: -0.3125rem;
  left: 0;
  top: 50%;
}

*:focus-visible {
  outline: 0.125rem solid #9346dc;
  outline-offset: 0.125rem;
}

.skip-links {
  padding: 0;
  margin: 0;
  list-style: none;
}

.skip-links a {
  position: absolute;
  top: -10rem;
  left: 0.5rem;
  z-index: 100000;
  display: inline-block;
  padding: 0.55em 1.5em 0.5em;
  background-color: #9346dc;
  border: 0.125rem solid #9346dc;
  color: #000;
  border-radius: 0.25em;
  /* link necessary styles */
  text-decoration: none;
}

.skip-links a:focus {
  top: 0.75rem;
  outline: 0;
}

#header-nav {
  transition: top 0.7s ease-in-out, background-color 0.7s ease-in-out, translate 0.7s ease-in-out !important;
  background: rgba(95, 95, 95, 0.3);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(2.1px);
}
#header-nav.hide {
  top: -100%;
}
#header-nav.show {
  top: 0;
}
#header-nav:not(.top) {
  background: rgba(23, 3, 53, 0.69);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
}
#header-nav a {
  padding: 0.5rem 0.5rem;
}
#header-nav a:hover {
  text-decoration: underline;
}
#header-nav .active {
  background-color: #66498d;
  border-radius: 0.375rem;
  color: #fff;
}

.navbar-brand .logo__img {
  width: 2rem;
}

.home-banner {
  background-image: linear-gradient(to bottom, rgba(64, 31, 79, 0.6), rgba(82, 75, 83, 0.6)), url("/images/home-banner.webp");
  background-position: center center;
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
}

.home-banner .container {
  min-height: 80vh;
  padding: 3rem 1.5rem;
}

@media only screen and (min-width: 45em) {
  .home-banner .container {
    min-height: 70vh;
  }
}
.banner {
  background-image: linear-gradient(to bottom, rgba(64, 31, 79, 0.6), rgba(82, 75, 83, 0.6)), url("/images/home-banner.webp");
  background-position: center center;
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
}

#banner figure figcaption::before {
  content: "— ";
}

.banner .container {
  min-height: 50vh;
  padding: 3rem 1.5rem;
}

@media only screen and (min-width: 45em) {
  .banner .container {
    min-height: 35vh;
  }
}
#banner {
  background-color: rgb(48, 24, 50) !important;
}

.card-img-top {
  height: 10rem;
  object-fit: contain;
  padding: 1rem 2rem;
}

.card-group {
  padding-top: 1.5rem;
}

.skill__cards .card:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.4);
  transition: none;
}
.skill__cards .card:hover:after {
  width: 120%;
  background-color: rgba(255, 255, 255, 0);
  transition: all 0.7s ease-in-out;
}

.project__logo,
.project__type {
  min-width: 5rem;
  max-width: 8rem;
  max-height: 3rem;
  text-align: center;
}

.project__image {
  max-height: 13rem;
  object-fit: cover;
  object-position: top center;
}

.animate {
  opacity: 1;
}
.animate.in-view.fade__in {
  animation: fade__in 1.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}
.animate.in-view.fade__bottom {
  animation: fade__bottom 0.6s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}
.animate.in-view.fade__left {
  animation: fade__left 0.6s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}
.animate.in-view.fade__top {
  animation: fade__top 0.6s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}
.animate.in-view.fade__right {
  animation: fade__right 0.6s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}
.animate.in-view.slide__center {
  animation: slide__center 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both 0.75s;
}

footer .logo {
  display: flex;
  gap: 0.75rem;
  align-items: center;
  font-size: 1.525rem;
  font-weight: bold;
  line-height: 1.3;
  margin: 0;
}

footer .logo__img {
  width: 2rem;
}

footer .logo__text {
  font-size: 1.525rem;
  font-weight: 300;
  line-height: 1.3;
}

.footer__menu {
  list-style: none;
  padding: 0;
}
.footer__menu li {
  display: inline-block;
  padding: 0 0.95rem;
}
.footer__menu li a {
  text-decoration: none;
}
.footer__menu li a:hover {
  text-decoration: underline;
}

.to-top {
  background-color: #9346dc;
}

.to-top-container.visible {
  transform: translateY(0);
}

.to-top-container {
  position: fixed;
  bottom: 1rem;
  right: 1.5rem;
  transition: transform 0.2s, bottom 0.2s;
  cursor: pointer;
  transform: translateY(145%);
}
.to-top-container a {
  color: #fff;
  text-decoration: none;
}
.to-top-container:hover, .to-top-container:focus-visible {
  bottom: 1.5rem;
  color: #000;
  transform: translateY(0);
}

.screen-reader-text {
  position: absolute;
  clip-path: polygon(0 0, 0.1% 0, 0.1% 0.1%, 0 0.1%);
}